.header .options{text-align:center}.header ul.sideNav{list-style-type:none;margin:0;padding:0;width:25%;background-color:#f1f1f1;position:fixed;height:100%;overflow:auto;background:-webkit-gradient(linear, left top, left bottom, from(#f9fbf7), to(#C9D8FF));background:linear-gradient(to bottom, #f9fbf7, #C9D8FF)}.header ul.sideNav a{display:block;color:#000;padding:8px 16px;text-decoration:none}.header ul.sideNav a.active{background-color:#3d6def;color:white}.header ul.sideNav a:hover:not(.active){background-color:#3d6def;color:white}.header .logo-container{height:50px;padding:15px}.header .logo-container .logo{cursor:pointer;float:left}.header .logo-container .signout-icon{float:right;cursor:pointer}@media screen and (max-width: 1000px){.header ul.sideNav{width:100%;height:auto;position:relative}.header ul.sideNav a{float:left;padding:15px}.options{text-align:center}.navList{display:inline-block}}@media screen and (max-width: 500px){.header ul.sideNav a{text-align:center;float:none}.header .navList{display:inline-block}}

.skin{fill:#eab38f}.eyes{fill:#1f1f1f}.hair{fill:#2f1b0d}.line{fill:none;stroke:#2f1b0d;stroke-width:2px}.center{padding:5px}.card{width:350px;height:250px;background-color:#fff;background:-webkit-gradient(linear, left top, left bottom, from(#f8f8f8), to(#fff));background:linear-gradient(#f8f8f8, #fff);box-shadow:0 8px 16px -8px rgba(0,0,0,0.4);border-radius:6px;overflow:hidden;position:relative;margin:1.5rem auto}.card h1{text-align:left}.card .additional{position:absolute;width:65px;height:100%;background:-webkit-gradient(linear, left top, left bottom, from(#dE685E), to(#EE786E));background:linear-gradient(#dE685E, #EE786E);-webkit-transition:width 0.4s;transition:width 0.4s;overflow:hidden;z-index:2}.card.green .additional{background:-webkit-gradient(linear, left top, left bottom, from(#92bCa6), to(#A2CCB6));background:linear-gradient(#92bCa6, #A2CCB6)}.card:hover .additional{width:100%;border-radius:0 5px 5px 0}.card .additional .user-card{width:65px;height:100%;position:relative;float:left}.card .additional .user-card::after{content:"";display:block;position:absolute;top:10%;right:-2px;height:80%;border-left:2px solid rgba(0,0,0,0.025)}.card .additional .user-card .level,.card .additional .user-card .points{top:15%;color:#fff;text-transform:uppercase;font-size:0.75em;font-weight:bold;background:rgba(0,0,0,0.15);padding:0.125rem 0.75rem;border-radius:100px;white-space:nowrap}.card .additional .user-card .points{top:85%}.card .additional .user-card svg{top:50%}.card .additional .more-info{width:70%;float:right;position:absolute;left:70px;height:100%;overflow:hidden}.card .additional .more-info h1{color:#fff;margin-bottom:0;text-align:center}.card.green .additional .more-info h1{color:#224C36}.card .additional .coords{margin:1rem 3rem;color:#fff;font-size:1rem;font-weight:bold}.card.green .additional .coords{color:#325C46}.card .additional .coords span+span{float:right}.card .additional .icon{font-size:2rem;display:flex;position:absolute;bottom:1rem;left:1rem;right:1rem;top:auto;color:#fff;font-weight:bold}.card.green .additional .icon{color:#325C46}.card .additional .icon>div{flex:1 1;text-align:center}.card .additional .icon i{display:block}.card .additional .icon div.title{font-size:0.75rem;font-weight:bold;text-transform:uppercase;margin-bottom:5px}.card .additional .icon div.value{font-size:1.5rem;font-weight:bold;line-height:1.5rem}.card .additional .icon div.value.infinity{font-size:2.5rem}.card .general{width:80%;height:100%;position:absolute;top:0;right:0;z-index:1;box-sizing:border-box;padding:1rem;padding-top:0}.card .general .more{position:absolute;bottom:1rem;right:1rem;font-size:0.9em}@media screen and (max-width: 1000px){.center{padding:5px}}@media screen and (max-width: 500px){.center{padding:5px;margin:auto}.center .card{width:auto}}

.main{background:white;background-image:url(/react-todo-app/static/media/geo3.ab281892.png);background-position:center;background-repeat:no-repeat;background-size:cover;position:relative;font-family:Abel, Arial, Verdana, sans-serif;height:100%;width:100%}

.group{position:relative;margin:45px 20px 0px 0px}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}.group .form-textarea{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0;resize:none}.group .form-textarea:focus{outline:none}.group .form-textarea:focus ~ .form-textarea-label{top:-14px;font-size:12px;color:#000}.group .form-textarea-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-textarea-label.shrink{top:-14px;font-size:12px;color:#000}.toggle{position:relative;display:block;width:40px;height:20px;cursor:pointer;-webkit-tap-highlight-color:transparent;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}#cbx{display:none}.toggle:before{content:"";position:relative;top:3px;left:3px;width:34px;height:14px;display:block;background:#9A9999;border-radius:8px;-webkit-transition:background 0.2s ease;transition:background 0.2s ease}.toggle span{position:absolute;top:0;left:0;width:20px;height:20px;display:block;background:white;border-radius:10px;box-shadow:0 3px 8px rgba(154,153,153,0.5);-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.toggle span:before{content:"";position:absolute;display:block;margin:-18px;width:56px;height:56px;background:rgba(79,46,220,0.5);border-radius:50%;-webkit-transform:scale(0);transform:scale(0);opacity:1;pointer-events:none}#cbx:checked+.toggle:before{background:#85d5ff}#cbx:checked+.toggle span{background:#df8bfc;-webkit-transform:translateX(20px);transform:translateX(20px);-webkit-transition:all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25),background 0.15s ease;transition:all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25),background 0.15s ease;box-shadow:0 3px 8px rgba(79,46,220,0.2)}#cbx:checked+.toggle span:before{-webkit-transform:scale(1);transform:scale(1);opacity:0;-webkit-transition:all 0.4s ease;transition:all 0.4s ease}

.custom-button{max-width:100%;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white;padding:0 10px}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}

.todo{display:inline-block;background-color:white;padding:20px 20px;border-radius:10px;position:-webkit-sticky;position:sticky;top:15px;font-size:20px;width:auto;height:auto}.todo .title{margin:10px 0}.todo span{display:block;-webkit-margin-before:0.83em;margin-block-start:0.83em;-webkit-margin-after:0.83em;margin-block-end:0.83em;-webkit-margin-start:0px;margin-inline-start:0px;-webkit-margin-end:0px;margin-inline-end:0px;font-weight:bold}.todo .button{display:inline-block;width:100%}.todo form{display:inline-block}

.aside{text-align:center;height:100%;width:100%;display:inherit}

.main{grid-area:main}.aside{grid-area:aside}.homepage{align-items:center;margin-left:25%;padding:15px 1rem;background-color:#ddd;display:grid;grid-template-areas:'main aside';grid-template-columns:auto 350px;grid-gap:1rem}@media screen and (max-width: 1000px){.homepage{margin-left:0;padding:15px 15px;height:auto;display:grid;grid-template-areas:'main' 'aside';grid-template-columns:auto;grid-template-rows:auto 500px;grid-gap:1rem}}@media screen and (max-width: 500px){.homepage{margin-left:0;padding:15px 15px;height:auto;display:grid;grid-template-areas:'main' 'aside';grid-template-columns:auto;grid-template-rows:auto 500px;grid-gap:1rem}}

.sign-in{border-top-left-radius:10px;border-bottom-left-radius:10px;padding:20px 40px;position:relative;border-right-style:outset}.sign-in .title{margin:10px 0}.sign-in .buttons{display:flex;justify-content:space-between}

.sign-up{padding:20px 40px;position:relative}.sign-up .title{margin:10px 0}

.sign-up{grid-area:signUp}.sign-in{grid-area:signIn}.page-content{width:100%;margin:0 auto;background:#373B44;background:-webkit-gradient(linear, left top, right top, from(#4286f4), to(#373B44));background:linear-gradient(to right, #4286f4, #373B44);min-height:100vh;display:grid;display:-webkit-grid;justify-content:center;-o-justify-content:center;-ms-justify-content:center;-moz-justify-content:center;-webkit-justify-content:center;align-items:center;-o-align-items:center;-ms-align-items:center;-moz-align-items:center;-webkit-align-items:center}.sign-in-and-sign-up{background:#fff;width:100%;border-radius:10px;-o-border-radius:10px;-ms-border-radius:10px;-moz-border-radius:10px;-webkit-border-radius:10px;box-shadow:0px 8px 20px 0px rgba(0,0,0,0.15);-o-box-shadow:0px 8px 20px 0px rgba(0,0,0,0.15);-ms-box-shadow:0px 8px 20px 0px rgba(0,0,0,0.15);-moz-box-shadow:0px 8px 20px 0px rgba(0,0,0,0.15);-webkit-box-shadow:0px 8px 20px 0px rgba(0,0,0,0.15);margin:175px 0;position:relative;display:grid;display:-webkit-grid;grid-template-areas:'signIn signUp';grid-template-columns:auto auto;grid-gap:1rem;font-family:'Open Sans', sans-serif}@media screen and (max-width: 760px){.sign-in-and-sign-up{height:auto;display:grid;grid-template-areas:'signIn' 'signUp';grid-template-columns:auto;grid-gap:1rem}.sign-in{border-bottom-left-radius:0;border-right-style:none;border-bottom-style:outset;padding-bottom:40px}}@media screen and (max-width: 500px){.sign-in-and-sign-up{height:auto;display:grid;grid-template-areas:'signIn' 'signUp';grid-template-columns:auto;grid-gap:1rem}.sign-in{border-bottom-left-radius:0;border-right-style:none;border-bottom-style:outset;padding-bottom:40px}}

