@import url('https://fonts.googleapis.com/css?family=Abel');
.skin { fill: #eab38f; }
.eyes { fill: #1f1f1f; }
.hair { fill: #2f1b0d; }
.line { fill: none; stroke: #2f1b0d; stroke-width:2px; }



.center {
padding: 5px;
}

.card {
  width: 350px;
  height: 250px;
  background-color: #fff;
  background: linear-gradient(#f8f8f8, #fff);
  box-shadow: 0 8px 16px -8px rgba(0,0,0,0.4);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
  margin: 1.5rem auto;
}

.card h1 {
  text-align: left;
}

.card .additional {
  position: absolute;
  width: 65px;
  height: 100%;
  background: linear-gradient(#dE685E, #EE786E);
  transition: width 0.4s;
  overflow: hidden;
  z-index: 2;
}

.card.green .additional {
  background: linear-gradient(#92bCa6, #A2CCB6);
}


.card:hover .additional {
  width: 100%;
  border-radius: 0 5px 5px 0;
}

.card .additional .user-card {
  width: 65px;
  height: 100%;
  position: relative;
  float: left;
}

.card .additional .user-card::after {
  content: "";
  display: block;
  position: absolute;
  top: 10%;
  right: -2px;
  height: 80%;
  border-left: 2px solid rgba(0,0,0,0.025);
}

.card .additional .user-card .level,
.card .additional .user-card .points {
  top: 15%;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: bold;
  background: rgba(0,0,0,0.15);
  padding: 0.125rem 0.75rem;
  border-radius: 100px;
  white-space: nowrap;
}

.card .additional .user-card .points {
  top: 85%;
}

.card .additional .user-card svg {
  top: 50%;
}

.card .additional .more-info {
  width: 70%;
  float: right;
  position: absolute;
  left: 70px;
  height: 100%;
  overflow: hidden;
}

.card .additional .more-info h1 {
  color: #fff;
  margin-bottom: 0;
  text-align: center;
}

.card.green .additional .more-info h1 {
  color: #224C36;
}

.card .additional .coords {
  margin: 1rem 3rem;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
}

.card.green .additional .coords {
  color: #325C46;
}

.card .additional .coords span + span {
  float: right;
}

.card .additional .icon {
  font-size: 2rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  right: 1rem;
  top: auto;
  color: #fff;
  font-weight: bold;
}

.card.green .additional .icon {
  color: #325C46;
}

.card .additional .icon > div {
  flex: 1;
  text-align: center;
}

.card .additional .icon i {
  display: block;
}

.card .additional .icon div.title {
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.card .additional .icon div.value {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.card .additional .icon div.value.infinity {
  font-size: 2.5rem;
}

.card .general {
  width: 80%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  box-sizing: border-box;
  padding: 1rem;
  padding-top: 0;
}

.card .general .more {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  font-size: 0.9em;
}



@media screen and (max-width: 1000px) {
    .center {
        padding: 5px;
    }
}
    

    
@media screen and (max-width: 500px) {
    .center {
        padding: 5px;
        // width: 90%;
        margin: auto;

        .card {
            width: auto;
        }
    }
    
    
}